import React from 'react'
import { assetUrl, baseUrl } from '../utils/constant';

function CompareNfts() {
  return (
    
    <main className="main-spacing">
    {/*  Submit NFTs form start  */}
    <section className="compare-nft">
        <div className="nft-container">
            <div className="container-lg">
                <div className="row m-0">
                    <div className="col-12 heading">
                        <h1> <span className="highlight">Compare</span> NFTs </h1>
                    </div>
                    <div className="col-lg-9 left-block">
                        <div className="compare-nft-form">
                          <form action="" method="post" id="compare-nft-form">
                            <div className="box-modal">
                              <div className="select-group">
                                <div className="form-group">
                                    <label for="select_a">Select A</label>
                                    <input type="text" name="select_a" id="select_a" className="form-control" placeholder='Eg. Etherneum' />
                                </div>
                                <div className="compare-sign">
                                  <button className='compare-btn' type='button'>
                                    <img src="./images/compare-icon.png" alt="Compare icon" />
                                  </button>
                                </div>
                                <div className="form-group">
                                    <label for="select_b">Select B</label>
                                    <input type="text" name="select_b" id="select_b" className="form-control" placeholder='Eg. Bitcoin' />
                                </div>
                              </div>

                              <div className="diff-result">
                                <div className="result-containers">
                                  <div className="result-box">
                                    <div className="box-wrapper">
                                      <div className="header">
                                        <div className="image">
                                          <img src={assetUrl+"images/user-dp.png"} alt="User image" height='62' width='62'/>
                                        </div>
                                        <h3>Etherneum</h3>
                                      </div>
                                      <div className="body">
                                        <div className="stats">
                                          <div className="item">
                                            <div className="title">
                                              Market Cap
                                            </div>
                                            <div className="number">
                                              188
                                            </div>
                                          </div>
                                          <div className="item">
                                            <div className="title">
                                              Volume
                                            </div>
                                            <div className="number">
                                              3.79
                                            </div>
                                          </div>
                                          <div className="item br-none">
                                            <div className="title">
                                              Average
                                            </div>
                                            <div className="number">
                                              .04
                                            </div>
                                          </div>
                                          <div className="item">
                                            <div className="title">
                                              Floor
                                            </div>
                                            <div className="number">
                                              .028
                                            </div>
                                          </div>
                                          <div className="item">
                                            <div className="title">
                                              Sale
                                            </div>
                                            <div className="number">
                                              94
                                            </div>
                                          </div>
                                          <div className="item">
                                          </div>
                                        </div>
                                      </div>
                                    </div>
                                  </div>
                                  <div className="result-box">
                                    <div className="box-wrapper">
                                      <div className="header diff-head">
                                        <div className="small-heading">Difference</div>
                                        <h3 className="heading">Etherneum <span className='highlight'>vs</span> Bitcoin </h3>
                                      </div>
                                      <div className="body">
                                        <div className="stats diff-stats">
                                          <div className="item">
                                            <div className="title">
                                              Market Cap
                                            </div>
                                            <div className="number">
                                              188
                                            </div>
                                          </div>
                                          <div className="item br-none">
                                            <div className="title">
                                              Average
                                            </div>
                                            <div className="number">
                                              .04
                                            </div>
                                          </div>
                                          <div className="item">
                                            <div className="title">
                                              Volume
                                            </div>
                                            <div className="number">
                                              3.79
                                            </div>
                                          </div>
                                          <div className="item">
                                            <div className="title">
                                              Sale
                                            </div>
                                            <div className="number">
                                              94
                                            </div>
                                          </div>
                                        </div>
                                      </div>
                                    </div>
                                  </div>
                                  <div className="result-box last-box">
                                    <div className="box-wrapper">
                                      <div className="header">
                                        <div className="image">
                                          <img src={assetUrl+"images/user-dp.png"} alt="User image" />
                                        </div>
                                        <h3>Etherneum</h3>
                                      </div>
                                      <div className="body">
                                        <div className="stats">
                                          <div className="item">
                                            <div className="title">
                                              Market Cap
                                            </div>
                                            <div className="number">
                                              188
                                            </div>
                                          </div>
                                          <div className="item">
                                            <div className="title">
                                              Volume
                                            </div>
                                            <div className="number">
                                              3.79
                                            </div>
                                          </div>
                                          <div className="item br-none">
                                            <div className="title">
                                              Average
                                            </div>
                                            <div className="number">
                                              .04
                                            </div>
                                          </div>
                                          <div className="item">
                                            <div className="title">
                                              Floor
                                            </div>
                                            <div className="number">
                                              .028
                                            </div>
                                          </div>
                                          <div className="item">
                                            <div className="title">
                                              Sale
                                            </div>
                                            <div className="number">
                                              94
                                            </div>
                                          </div>
                                          <div className="item">
                                          </div>
                                        </div>
                                      </div>
                                    </div>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </form>
                        </div>

                        <div className="comparison-graph">
                          <div className="box-modal">
                            <div className="graph-wrapper">
                              <img src={baseUrl+"images/graph.png"} alt="Graph" />
                            </div>
                          </div>
                        </div>
                    </div>
                    
                    <div className="col-lg-3 right-block">
                        <div className="image">
                            <img src="./images/img3.png" alt="dummy image" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
    {/*  Submit NFTs form end  */}

</main>
  )
}

export default CompareNfts